import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Box from "@mui/material/Box";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { CircularProgress, Typography } from "@mui/material";
import SurveyService from "~/services/SurveyService";

import { onApiError } from "~/utils/errorsProcessor";
import {
  formatlocalDateTime,
  serverDateTimeToLocalDateTime,
} from "~/utils/DateUtils";
import styles from "./ResponseEvent.module.css";
import { previewUrl } from "~/networking/run";
import { useDispatch } from "react-redux";
import { onError } from "~/state/edit/editState";

function ResponsesEvent({ responseId }) {
  const { t } = useTranslation("manage");
  const [fetching, setFetching] = useState(true);
  const dispatch = useDispatch();

  const processApirror = (e) => {
    setFetching(false);
    onApiError({
      error: e,
      globalErrorHandler: (processedError) => {
        dispatch(onError(processedError));
      },
    });
  };

  const [eventResponse, setEventResponse] = useState(null);

  const fetchResponses = () => {
    setFetching(true);
    SurveyService.eventResponse(responseId)
      .then((data) => {
        if (data) {
          setEventResponse(data.data);
        }
        setFetching(false);
      })
      .catch((err) => {
        processApirror(err);
      });
  };

  useEffect(() => {
    fetchResponses();
  }, []);

  return (
    <Box className={styles.mainContainer}>
      {fetching ? (
        <div className={styles.loadingWrapper}>
          <CircularProgress />
        </div>
      ) : (
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              {eventResponse && (
                <TableRow>
                  <TableCell key="name" align="left">
                    {t("event_response.name")}
                  </TableCell>
                  <TableCell key="time" align="left">
                    {t("event_response.time")}
                  </TableCell>
                  <TableCell key="details" align="left">
                    {t("event_response.details")}
                  </TableCell>
                </TableRow>
              )}
            </TableHead>
            <TableBody>
              {eventResponse?.map((event) => {
                return (
                  <TableRow key={event.time} sx={{ minHeight: "100px" }}>
                    <TableCell key="name" align="left">
                      {event.name}
                    </TableCell>
                    <TableCell key="time" align="left">
                      {formatlocalDateTime(
                        serverDateTimeToLocalDateTime(event.time)
                      )}
                    </TableCell>
                    <TableCell key="details" align="left">
                      {event.name == "Navigation" ? (
                        <>
                          <Box>
                            <strong>{t("label.from")}</strong>: {event.from}
                          </Box>
                          <Box>
                            <strong>{t("label.to")}</strong>: {event.to}
                          </Box>
                          <Box>
                            <strong>{t("event_response.direction")}</strong>:{" "}
                            {event.direction?.name}
                          </Box>
                        </>
                      ) : event.name == "VoiceRecording" ? (
                        <audio controls>
                          <source
                            src={previewUrl(event.fileName)}
                            type="audio/mp4"
                          />
                          File not Supported
                        </audio>
                      ) : event.name == "Location" ? (
                        <>
                          <Box>
                            <strong>{t("label.latitude")}</strong>:{" "}
                            {event.latitude}
                          </Box>
                          <Box>
                            <strong>{t("label.longitude")}</strong>:{" "}
                            {event.longitude}
                          </Box>
                        </>
                      ) : (
                        <Box>
                          <strong>{t("label.code")}</strong>: {event.code}
                        </Box>
                      )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Box>
  );
}

export default React.memo(ResponsesEvent);
